<template>
  <v-dialog
    v-model="this.$store.state.dialog_edit"
    max-width="1000px"
    persistent
  >
    <v-card>
      <v-card-title
        class="primary"
      >
        <span class="text-h5 white--text">Edit Order</span>
        <v-spacer />
        <v-btn
          icon
          @click="$store.commit('dialog_edit', false)"
        >
          <v-icon dark>
            mdi-close
          </v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text class="pa-5">
        <v-form>
          <v-container class="py-0">
            <v-row>
              <v-col>
                <v-select
                  v-model="form.selectedUser"
                  :items="this.$store.state.customer_list"
                  item-text="name"
                  item-value="id"
                  label="Select User"
                  disabled
                />
                <div class="text-h3">
                  Product
                </div>
                <v-text-field
                  v-model="form.productName"
                  label="Product Name"
                  outlined
                  clearable
                />
                <v-text-field
                  v-model="form.weight"
                  label="Weight"
                  type="number"
                  outlined
                  clearable
                  @change="countFee"
                />
                <v-text-field
                  v-model="form.volume"
                  label="Volume"
                  type="number"
                  outlined
                  clearable
                />
                <v-text-field
                  v-model="form.delivery_fee"
                  label="Delivery Fee"
                  outlined
                  readonly
                />
                <v-text-field
                  v-model="form.price"
                  label="Price"
                  type="number"
                  outlined
                  clearable
                />
              </v-col>
              <v-col>
                <v-textarea
                  v-model="form.description"
                  label="Description"
                  type="number"
                  outlined
                  clearable
                />
                <v-file-input
                  v-model="form.photo"
                  prepend-icon="mdi-camera"
                  label="Photo"
                  clearable
                  outlined
                  @change="preview($event)"
                />
                <v-card
                  class="d-flex justify-space-around pa-4"
                  style="border:dashed; border-color: gray;"
                >
                  <v-img
                    v-if="imgUrl"
                    class="preview"
                    :src="imgUrl"
                  />
                  <v-card-text
                    v-else
                    class="text-center"
                    style="height:200px"
                  >
                    <v-img
                      class="preview"
                      :src="$baseUrl+form.photo"
                    />
                  </v-card-text>
                </v-card>
              </v-col>

              <v-col>
                <div class="text-h3">
                  Delivery Data
                </div>
                <v-text-field
                  v-model="form.receiverName"
                  label="Receiver Name"
                  outlined
                  clearable
                />
                <v-text-field
                  v-model="form.receiverPhone"
                  label="Receiver Phone"
                  outlined
                  clearable
                />
                <v-text-field
                  v-model="form.receiverPhone2"
                  label="Receiver Phone"
                  outlined
                  clearable
                />
                <v-select
                  v-model="form.selectedDistrict"
                  :items="this.$store.state.district"
                  item-text="nama"
                  item-value="id"
                  label="Select District"
                  outlined
                  clearable
                />
                <v-textarea
                  v-model="form.receiverAddress"
                  label="Receiver Address"
                  outlined
                  clearable
                />
                <v-select
                  v-model="form.selectedMethod"
                  :items="this.$store.state.methods"
                  item-text="method"
                  item-value="id"
                  label="Select Payment Method"
                  outlined
                  clearable
                />
                <v-btn
                  color="primary"
                  min-width="150"
                  class="float-right"
                  @click="updateOrder"
                >
                  Update Order
                </v-btn>
                <!-- <v-btn
                  color="primary"
                  min-width="150"
                  class="float-right"
                  @click="$store.commit('dialog_edit', false)"
                >
                  Close
                </v-btn> -->
              </v-col>
            </v-row>
          </v-container>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
  import axios from 'axios'

  export default {
    // eslint-disable-next-line vue/require-prop-types
    props: ['form'],
    data () {
      return {
        imgUrl: this.form.photo,
      }
    },
    methods: {
      updateOrder () {
        this.$swal.showLoading()
        const form = new FormData()
        form.append('order_id', this.form.order_id)
        form.append('name', this.form.productName)
        form.append('weight', this.form.weight)
        form.append('volume', this.form.volume)
        form.append('price', this.form.price)
        form.append('description_order', this.form.description)
        form.append('photo', this.form.photo)
        form.append('receiver_name', this.form.receiverName)
        form.append('receiver_phone', this.form.receiverPhone)
        form.append('receiver_phone2', this.form.receiverPhone2)
        form.append('receiver_address', this.form.receiverAddress)
        form.append('district', this.form.selectedDistrict)
        form.append('payment_method', this.form.selectedMethod)
        form.append('user_id', this.form.selectedUser)
        form.append('delivery_fee', this.form.delivery_fee)
        form.append('delivery_address_id', this.form.delivery_address_id)
        axios.post('admin/order/update', form).then(() => {
          this.$swal(
            'Data Created Successfully',
            '',
            'success',
          )
          this.$store.commit('dialog_edit', false)
          this.form.order_id = 0
          this.form.productName = null
          this.form.weight = 0
          this.form.volume = 0
          this.form.price = 0
          this.form.description = null
          this.form.photo = null
          this.form.receiverName = null
          this.form.receiverPhone = null
          this.form.receiverAddress = null
          this.form.selectedDistrict = null
          this.form.selectedMethod = null
          this.form.selectedUser = null
          this.form.delivery_address_id = 0
          this.form.delivery_fee = 0
        })
          .catch(error => {
            console.log(error)
          })
      },

      countFee () {
        this.form.delivery_fee = 0
        const w = parseInt(this.form.weight)
        const list = this.$store.state.del_fee_list
        list.forEach(item => {
          if (w > parseInt(item.from_weight) && w <= parseInt(item.to_weight)) {
            this.form.delivery_fee = parseInt(item.price)
          }
        })
      },

      preview (e) {
        if (this.form.photo !== null) {
          this.form.photo = e
          this.imgUrl = URL.createObjectURL(this.form.photo)
        } else {
          this.imgUrl = null
        }
      },
    },
  }
</script>
